.rsvp-section {
	position: relative;
	padding: 8rem 0 5rem;
	&.no-prokes {
		padding: 5rem 0;
	}
	@include max-md {
		padding: 14rem 0 3rem;
		&.no-prokes {
			padding: 3rem 0;
		}
	}
	.orn-1 {
		position: absolute;
		right: 0;
		bottom: 5rem;
		width: 340px;
		transform: translateX(45%) scaleX(-1);
		@include max-md {
			transform: translateX(40%) scaleX(-1);
		}
		img {
			transform-origin: 100% 100%;
		}
	}
	.brush-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		transform: translateX(40%);
	}
	.elements-wrapper-absolute {
		.background-element {
			border-radius: var(--border-radius);
			overflow: hidden;
		}
	}
	.image-wrapper {
		position: relative;
		height: 100%;
		width: auto;
		margin: 0 auto;
		border-radius: 1000px 1000px 60px 60px;
		aspect-ratio: 9/10;
		z-index: 5;
		@include max-md {
			height: auto;
			aspect-ratio: 5/6;
		}
		.rsvp-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 1000px 1000px 60px 60px;
		}
	}
	.rsvp-wrapper {
		position: relative;
		.rsvp-content {
			position: relative;
			padding: 12rem 2rem 0;
			border-radius: var(--border-radius);
			background-color: var(--color-primary);
			margin-top: -10rem;
			@include max-md {
				margin-top: -8rem;
				padding: 10rem 1rem 0;
			}
			.rsvp-form {
				position: relative;
				.btn-custom {
					transform: translateY(50%);
				}
			}
		}
	}
}
