.wishes-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		left: 0;
		top: 3rem;
		width: 340px;
		transform: translateX(-55%) scaleX(-1);
		@include max-md {
			transform: translateX(-60%) scaleX(-1);
		}
		img {
			transform-origin: 100% 100%;
		}
	}
	.brush-1 {
		position: absolute;
		top: 0;
		right: 0;
		width: 240px;
	}
	.image-wrap {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		.wishes-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	.card-form {
		margin-bottom: 0;
		background-color: transparent;
		position: relative;
		z-index: 3;
		border-radius: 10px;
		&.card {
			border: none;
		}
		.card-body {
			padding: 0;
		}
	}

	.wishes-preview {
		position: relative;
		padding: 3rem 0;
		.wishes {
			height: 30rem;
			overflow: auto;
			padding-right: 1rem;
			display: grid;
			grid-template-columns: 1fr 1fr 1fr;
			gap: 0.75rem;
			&::-webkit-scrollbar {
				width: 5px;
			}
			&::-webkit-scrollbar-track {
				background-color: #eee;
				border-radius: 20px;
				opacity: 0.15;
			}
			&::-webkit-scrollbar-thumb {
				background-color: var(--color-secondary);
				border-radius: 20px;
			}
			@include max-md {
				grid-template-columns: 1fr;
			}
			.wish {
				display: flex;
				justify-content: space-between;
				flex-direction: column;
				width: 100%;
				height: 12rem;
				padding: 1rem;
				overflow: auto;
				background-color: var(--color-light);
				border-radius: 5px;
				&::-webkit-scrollbar {
					width: 5px;
				}
				&::-webkit-scrollbar-track {
					background-color: #eee;
					border-radius: 20px;
					opacity: 0.15;
				}
				&::-webkit-scrollbar-thumb {
					background-color: var(--color-secondary);
					border-radius: 20px;
				}
				.wish-description {
					position: relative;
				}
			}
		}
	}
}
