.story-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		left: 0;
		top: 25rem;
		width: 240px;
		z-index: 5;
		.img-fluid:nth-child(1) {
			position: absolute;
			top: 0;
			left: 0;
			width: 80%;
		}
		@include max-lg {
			width: 200px;
			top: 15rem;
			transform: translateX(-20%);
		}
		@include max-md {
			top: 8rem;
			width: 120px;
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		right: 0;
		top: 25rem;
		width: 240px;
		z-index: 5;
		.img-fluid:nth-child(1) {
			position: absolute;
			top: 0;
			left: 0;
			width: 80%;
		}
		@include max-lg {
			width: 200px;
			top: 15rem;
			transform: translateX(20%) scaleX(-1);
		}
		@include max-md {
			top: 8rem;
			width: 120px;
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.brush-3 {
		position: absolute;
		top: 50%;
		left: 50%;
		height: 120%;
		transform: translate(-50%, -50%);
	}
	.story-content {
		position: relative;
		border-radius: var(--border-radius);
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;
	}
	.story-description {
		position: relative;
		text-align: center;
		padding: 2rem;
		@include max-md {
			padding: 2rem 0;
		}
		* {
			position: relative;
		}
	}
	.image-story-wrap {
		position: relative;
		width: 100%;
		aspect-ratio: 16/9;
		border-radius: var(--border-radius);
		z-index: 3;
		@include max-md {
			aspect-ratio: 4/3;
		}
		.story-image {
			object-fit: cover;
			width: 100%;
			height: 100%;
			border-radius: var(--border-radius);
		}
	}
}
