.stream-section {
	position: relative;
	padding: 5rem 0 0;
	margin-bottom: 5rem;
	text-align: center;
	@include max-md {
		padding: 3rem 0 0;
		margin-bottom: 3rem;
	}
	.elements-wrapper-absolute {
		height: 70%;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 320px;
		transform: translateX(-30%);
		@include max-md {
			transform: translateX(-40%);
		}
		img {
			transform-origin: 100% 100%;
		}
	}
	.stream-wrapper {
		position: relative;
		border-radius: var(--border-radius);
		z-index: 5;
		.stream-content {
			position: relative;
			padding: 5rem 2rem 3rem;
			background-color: var(--color-primary);
			border-radius: 1000px 1000px 60px 60px;
			@include max-md {
				padding: 5rem 0.5rem 2rem;
			}
			h4 {
				@include max-md {
					text-align: center;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.card {
		background-color: transparent;
		height: 100%;
		color: var(--color-dark);
		border: none;
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
		transform: translateY(50%);
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		width: 100%;
		margin-top: 3rem;
		@include max-md {
			text-align: center;
		}
	}
}
