.header-section {
	position: relative;
	min-height: 100vh;
	width: 100%;
	.scroll-icon {
		position: absolute;
		top: 100%;
		left: 50%;
		transform: translate(-50%, -50%);
		z-index: 5;
	}
	.orn-1 {
		position: absolute;
		left: 0;
		bottom: -3rem;
		width: 240px;
		@include max-md {
			bottom: -4rem;
			width: 200px;
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		right: 0;
		bottom: -3rem;
		width: 240px;
		@include max-md {
			bottom: -4rem;
			width: 200px;
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.elements-absolute {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		.background-element {
			display: block;
			height: 100%;
			.background-wrapper {
				width: 100%;
				height: 100%;
				display: inline-block;
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		aspect-ratio: 3/4;
		overflow: hidden;
		border-radius: 1000px 1000px 60px 60px;
		.background-header {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 1000px 1000px 60px 60px;
		}
	}
	.header-content {
		position: relative;
		text-align: center;
		border-radius: 12px;
		.header {
			position: relative;
			width: 100%;
			height: 100%;
			padding: 4rem 0 2rem;
			@include max-lg {
				padding: 5rem 0 2rem;
			}
			.header-title {
				position: relative;
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}
