.apology-section {
	position: relative;
	padding: 5rem 0;
	margin-top: 5rem;
	background-color: var(--color-main);
	@include max-md {
		padding: 3rem 0;
		margin-top: 3rem;
	}
	.orn-1 {
		position: absolute;
		top: 50%;
		left: 0;
		transform: translateY(-50%);
		width: 240px;
		@include max-lg {
			width: 160px;
			transform: translateY(-60%);
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 50%;
		right: 0;
		transform: translateY(-50%) scaleX(-1);
		width: 240px;
		@include max-lg {
			width: 160px;
			transform: translateY(-60%) scaleX(-1);
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.appology-wrap {
		position: relative;
		padding: 5rem 2rem 2rem;
		border-radius: 1000px 1000px 60px 60px;
		&::before {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background-color: var(--color-primary);
			opacity: 0.7;
			content: "";
			border-radius: 1000px 1000px 60px 60px;
		}
		@include max-md {
			padding: 3rem 1rem 2rem;
		}
	}
}
