.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	height: 100vh;
	width: 100%;
	.orn-1 {
		position: absolute;
		left: 0;
		bottom: 0;
		width: 240px;
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 280px;
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		@include max-lg {
			padding: 4rem 0;
			align-items: center;
		}
		.cover-content {
			text-align: center;
			position: relative;
			background-color: transparent;
			border-radius: 12px;
			color: var(--color-dark);
			height: 100%;
			display: flex;
			justify-content: center;
			align-items: center;
			.cover-body {
				overflow: hidden;
				border-radius: var(--border-radius);
				position: relative;
				padding: 2rem;
				@include max-md {
					padding: 1rem 1rem 2rem;
				}
				.greeting-wrapper {
					position: relative;
					@include max-md {
						font-size: 14px;
					}
					h6 {
						font-weight: 700;
					}
				}
				.image-wrapper {
					position: relative;
					width: 100%;
					height: auto;
					aspect-ratio: 5/6;
					border-radius: 1000px 1000px 60px 60px;
					@include max-md {
						width: 80%;
						margin: 0 auto;
					}
					.cover-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
						border-radius: 1000px 1000px 60px 60px;
					}
				}
				.cover-description {
					padding: 1.5rem 1.5rem 2rem;
					text-align: center;
					.cover-event {
						span {
							font-weight: 600;
						}
					}
				}
			}
			.elements-widget {
				width: 100%;
			}
		}
	}
}
