.couple-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		left: 0;
		top: 1rem;
		width: 320px;
		transform: translateX(-42%);
		@include max-md {
			width: 240px;
		}
		img {
			transform-origin: 100% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		right: 0;
		top: 1rem;
		width: 320px;
		transform: translateX(42%) scaleX(-1);
		@include max-md {
			width: 240px;
		}
		img {
			transform-origin: 100% 100%;
		}
	}
	.brush-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 200px;
	}
	.brush-2 {
		position: absolute;
		top: 2rem;
		right: 0;
		width: 160px;
	}
	.elements-absolute {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 60%;
		.background-element {
			display: block;
			height: 100%;
			.background-wrapper {
				width: 100%;
				height: 100%;
				display: inline-block;
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
		}
	}
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		color: var(--color-dark);
		display: flex;
		justify-content: center;
		align-items: center;
		width: 50px;
		aspect-ratio: 1;
		position: relative;
		z-index: 3;
		span {
			position: relative;
			font-size: 24px;
			font-family: $font-secondary;
		}
	}
	.couple-content {
		position: relative;
	}
	.couple {
		position: relative;
		text-align: center;
		padding: 1rem;
		border-radius: 12px;
		.image-wrap {
			width: 80%;
			margin: 0 auto;
			position: relative;
			aspect-ratio: 9/10;
			border-radius: 1000px 1000px 60px 60px;
			&::before {
				position: relative;
				display: block;
				width: 2px;
				height: 2rem;
				margin: 0 auto 1rem;
				background-color: var(--color-secondary);
				border-radius: var(--border-radius);
				content: "";
			}
			.couple-image {
				height: 100%;
				object-fit: cover;
				position: relative;
				width: 100%;
				border-radius: 1000px 1000px 60px 60px;
			}
		}
		.couple-description {
			position: relative;
			padding: 2rem 1rem;
			background: radial-gradient(ellipse, #fff 0%, transparent 70%, transparent 100%);
		}
		.couple-parent {
			width: 100%;
			margin: auto;
		}
		.sosmed-wrap {
			position: relative;
			width: 100%;
			.sosmed {
				color: var(--text-button);
				text-decoration: none;
				padding: 0.5rem 1.5rem;
				border-radius: 50px;
				background: var(--color-secondary);
				border: 1px solid transparent;
				transition: all 0.25s ease;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
