.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		bottom: -2rem;
		left: 0;
		width: 240px;
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			transform: translateX(-50%);
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: -2rem;
		right: 0;
		width: 240px;
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			transform: translateX(50%) scaleX(-1);
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.elements-wrapper-absolute {
		height: 80%;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 5/6;
		overflow: hidden;
		border-radius: 1000px 1000px 60px 60px;
		.thank-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 1000px 1000px 60px 60px;
		}
	}
	.thank-wrapper {
		position: relative;
		overflow: hidden;
		.thank {
			position: relative;
			padding: 3rem 2rem;
			border-radius: 1000px;
			background: radial-gradient(ellipse, rgba(255, 255, 255) 0%, transparent 70%, transparent 100%);
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
