.gallery-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.brush-1 {
		position: absolute;
		top: 0;
		right: 0;
		width: 50%;
	}
	.img-gallery {
		height: auto;
		width: 100%;
		aspect-ratio: 3/4;
		object-fit: cover;
	}
	.splide {
		.splide__track {
			overflow: visible;
		}
		.splide__slide.is-active {
			.img-thumbnail {
				border: 3px solid #fff;
			}
		}
		.splide__arrow {
			border-radius: 0;
			width: 70px;
			height: 40px;
			background-color: var(--color-secondary);
			border-radius: 50px;
			@include max-lg {
				width: 50px;
				height: 30px;
			}
			svg {
				fill: var(--text-button);
				width: 25px;
				height: 35px;
			}
			&--prev {
				left: 0;
				transform: translate(-50%, -50%);
			}
			&--next {
				right: 0;
				transform: translate(50%, -50%);
			}
		}
	}
}
